       <div v-if="orderLoading">Оформление заказа...
        <div id="escapingBallG">
          <div id="escapingBall_1" class="escapingBallG"></div>
        </div>
       </div>

       <div class="cart__error form__error-block" v-if="orderLoadingFaled">
         <h4>Заявка не отправлена!</h4>
         <p>
           {{ formErrorMessage }}
         </p>
       </div>
     </form>
   </section>
 </main>
</template>

<script>
/* eslint-disable */
import BaseFormText from '@/components/BaseFormText.vue';
import BaseFormTextarea from '@/components/BaseFormTextarea.vue';
import { mapGetters } from 'vuex';
import numberFormat from '@/helpers/numberFormat';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

export default {
  components: { BaseFormText, BaseFormTextarea },
  data() {
    return {
      formData: {},
      formError: {},
      formErrorMessage: '',

      orderLoading: false,
      orderLoadingFaled: false,
    };
  },

  computed: {
    ...mapGetters({ products: 'cartDetailProducts', totalPrice: 'cartTotalPrice', totalAmount: 'cartTotalAmount' }),
  },

  filters: {
    numberFormat,
  },

  methods: {
    order() {
      this.orderLoading = true;
      this.orderLoadingFaled = false;

      clearTimeout(this.loadOrderTimer);
      this.formError = {};
      this.formErrorMessage = '';

      this.loadOrderTimer = setTimeout(() => {
        axios
        // eslint-disable-next-line
          .post(API_BASE_URL + '/api/orders', {
            ...this.formData,
          }, {
            params: {
              userAccessKey: this.$store.state.userAccessKey,
            },
          })
          .then(response => {
            this.$store.commit('resetCart');
            this.$store.commit('updateOrderInfo', response.data);
            this.$router.push({ name: 'orderInfo', params: { id: response.data.id }});
          })

          // eslint-disable-next-line
          .catch(error => {
            this.formError = error.response.data.error.request || {};
            this.formErrorMessage = error.response.data.error.message;
            this.orderLoadingFaled = true;
          })
          .then(() => {
            this.orderLoading = false;
          });
      }, 2000);
    },
  },
};
</script>

