<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
      <span class="content__info">
        152 товара
      </span>
    </div>

    <div class="content__catalog">
      <ProductFilter :price-from.sync="filtPriceFrom" :price-to.sync="filtPriceTo"
      :category-id.sync="filtCategoryId" :color-id.sync="filtColorId"/>

      <section class="catalog">

        <div v-if="productsLoading">Загрузка товаров...</div>
        <div v-if="productsLoadingFailed">Произошла ошибка при загрузке товаров
        <button @click.prevent="loadProducts">Попробовать еще раз</button>
        </div>

        <ProductList :products="products" />

        <BasePagination v-model="page" :count="countProducts" :per-page="productsPerPage" />

      </section>

    </div>
  </main>
</template>

<script>
import ProductList from '@/components/ProductList.vue';
import BasePagination from '@/components/BasePagination.vue';
import ProductFilter from '@/components/ProductFilter.vue';
import axios from 'axios';
import { API_BASE_URL } from '../config';

export default {
  components: { ProductList, BasePagination, ProductFilter },
  data() {
    return {
      filtPriceFrom: 0,
      filtPriceTo: 0,
      filtCategoryId: 0,
      filtColorId: 0,

      page: 1,
      productsPerPage: 3,

      productsData: null,

      productsLoading: false,
      productsLoadingFailed: false,
    };
  },
  computed: {
    products() {
      return this.productsData
      // eslint-disable-next-line
        ? this.productsData.items.map((product) => {
          return {
            ...product,
            image: product.image.file.url,
          };
        })
        : [];
    },
    countProducts() {
      return this.productsData ? this.productsData.pagination.total : 0;
    },
  },

  methods: {
    loadProducts() {
      this.productsLoading = true;
      this.productsLoadingFailed = false;
      clearTimeout(this.loadProductsTimer);
      this.loadProductsTimer = setTimeout(() => {
        axios
        // eslint-disable-next-line
        .get(API_BASE_URL + '/api/products', {
            params: {
              categoryId: this.filtCategoryId,
              colorId: this.filtColorId,
              page: this.page,
              limit: this.productsPerPage,
              minPrice: this.filtPriceFrom,
              maxPrice: this.filtPriceTo,
            },
          })
        // eslint-disable-next-line
          .then(response => this.productsData = response.data)
          // eslint-disable-next-line
          .catch(() => this.productsLoadingFailed = true)
          // eslint-disable-next-line
          .then(() => this.productsLoading = false);
      }, 500);
    },
  },
  watch: {
    page() {
      this.loadProducts();
    },
    filtPriceFrom() {
      this.loadProducts();
    },
    filtPriceTo() {
      this.loadProducts();
    },
    filtCategoryId() {
      this.loadProducts();
    },
    filtColorId() {
      this.loadProducts();
    },
  },

  created() {
    this.loadProducts();
  },
};
</script>
